import React from 'react';
import { connect } from 'react-redux';
import { Row, Select, Switch } from 'antd';
import {
  toggleCollapsedNav,
  onNavTypeChange,
  onNavStyleChange,
  onTopNavColorChange,
  onHeaderNavColorChange,
  onSwitchTheme,
  onDirectionChange,
} from 'redux/actions/Theme';
import { useThemeSwitcher } from 'react-css-theme-switcher';
import IntlMessage from '../util-components/IntlMessage';
import { THEME_CONFIG } from '../../configs/AppConfig';

const setLocale = (isLocaleOn, localeKey) => isLocaleOn ?
  <IntlMessage id={localeKey}/> : localeKey.toString();

const ListOption = ({
  name,
  selector,
  disabled,
  vertical,
}) => (<div className={`my-4 ${vertical
  ? ''
  : 'd-flex align-items-center justify-content-between'}`}>
  <div className={`${disabled ? 'opacity-0-3' : ''} ${vertical
    ? 'mb-3'
    : ''}`}>{name}</div>
  <div>{selector}</div>
</div>);

export const ThemeConfigurator = ({
  currentTheme,
  onHeaderNavColorChange,
  onSwitchTheme,
}) => {

  const {
    switcher,
    themes,
  } = useThemeSwitcher();

  const toggleTheme = (isChecked) => {
    onHeaderNavColorChange('');
    const changedTheme = isChecked ? 'dark' : 'light';

    localStorage.setItem('darkMode', String(changedTheme === 'dark'));

    onSwitchTheme(changedTheme);
    switcher({ theme: themes[changedTheme] });
  };

  const handleChange = (value) => {
    THEME_CONFIG.dataRefresh = value;
    localStorage.setItem('dataRefresh', value);
  };

  const handleCheck = (value) => {
    THEME_CONFIG.dashboardAlternation = value;
    localStorage.setItem('dashboardAlternation', value);
  };

  const DataRefresh = () => {

    return (<Row justify="space-between" align="middle" className="my-4">
      <div>Atualizao de Dados</div>
      <div>
        <Select defaultValue={THEME_CONFIG.dataRefresh} onChange={handleChange}>
          <Select.Option value="5sec">5 Segundos</Select.Option>
          <Select.Option value="1min">1 Minuto</Select.Option>
          <Select.Option value="5min">5 Minutos</Select.Option>
        </Select>
      </div>
    </Row>);

  };

  const RandomDashboard = () => {

    return (<Row justify="space-between" align="middle" className="my-4">
      <div>Alternncia de Dashboards</div>
      <div>
        <Switch defaultChecked={THEME_CONFIG.dashboardAlternation} onChange={handleCheck}/>
      </div>
    </Row>);

  };

  return (<>
    <div className="mb-5">

      <ListOption
        name={setLocale(true, 'dark_mode')}
        selector={<Switch checked={currentTheme ===
          'dark'} onChange={toggleTheme}/>}
      />

      <RandomDashboard/>

      <DataRefresh/>

    </div>

  </>);
};

const mapStateToProps = ({ theme }) => {
  const {
    navType,
    sideNavTheme,
    navCollapsed,
    topNavColor,
    headerNavColor,
    locale,
    currentTheme,
    direction,
  } = theme;
  return {
    navType,
    sideNavTheme,
    navCollapsed,
    topNavColor,
    headerNavColor,
    locale,
    currentTheme,
    direction,
  };
};

const mapDispatchToProps = {
  toggleCollapsedNav,
  onNavTypeChange,
  onNavStyleChange,
  onTopNavColorChange,
  onHeaderNavColorChange,
  onSwitchTheme,
  onDirectionChange,
};

export default connect(mapStateToProps, mapDispatchToProps)(ThemeConfigurator);
