import {
  SIDE_NAV_LIGHT, DIR_LTR, NAV_TYPE_TOP,
} from 'constants/ThemeConstant';
import { env } from './EnvironmentConfig';

export const APP_NAME = 'Aplipack';
export const API_BASE_URL = env.API_ENDPOINT_URL;
export const APP_PREFIX_PATH = '/dashboards';
export const AUTH_PREFIX_PATH = '/auth';

let darkMode = localStorage.getItem('darkMode') === 'true';

let dashboardAlternation = localStorage.getItem('dashboardAlternation') ===
  'true';
let dataRefresh = localStorage.getItem('dataRefresh');

if (!['5sec', '1min', '5min'].includes(dataRefresh)) dataRefresh = '5sec';

export const THEME_CONFIG = {
  navCollapsed: false,
  sideNavTheme: SIDE_NAV_LIGHT,
  locale: 'pt', // navType: NAV_TYPE_SIDE,
  navType: NAV_TYPE_TOP,
  topNavColor: '#3e82f7',
  headerNavColor: '',
  mobileNav: false,
  currentTheme: darkMode ? 'dark' : 'light',
  direction: DIR_LTR,
  dashboardAlternation: dashboardAlternation,
  dataRefresh: dataRefresh,
};

localStorage.setItem('darkMode', darkMode.toString());
localStorage.setItem('dashboardAlternation', dashboardAlternation.toString());
localStorage.setItem('dataRefresh', dataRefresh);
